// frontend/src/app/page.tsx
'use client';

import { useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/contexts/AuthContext';
import { Loader2 } from 'lucide-react';

/**
 * Esta página agora atua como um ponto de entrada que redireciona o usuário
 * com base em seu status de autenticação.
 */
export default function InitialRedirectPage() {
  const { loggedInAdmin, isLoadingAuth } = useAuth();
  const router = useRouter();

  useEffect(() => {
    // Não faz nada enquanto o estado de autenticação ainda está carregando do Local Storage
    if (isLoadingAuth) {
      return;
    }

    // Se o carregamento terminou e o admin está logado, vai para a página de questionários (dashboard)
    if (loggedInAdmin) {
      router.replace('/questionarios');
    } else {
      // Se o carregamento terminou e não há admin logado, vai para a página de login da empresa
      router.replace('/empresas/login');
    }
  }, [loggedInAdmin, isLoadingAuth, router]);

  // Exibe um loader em tela cheia enquanto a lógica de redirecionamento é processada
  // Em `layout.tsx`, as páginas de auth não têm o SideMenu, então este loader ocupará a tela inteira.
  return (
    <div className="flex h-screen w-full flex-col items-center justify-center bg-background">
      <div className="flex items-center space-x-3">
        <Loader2 size={32} className="animate-spin text-primary" />
        <p className="text-lg text-text-muted">Carregando sistema...</p>
      </div>
    </div>
  );
}